const uni = require('uni').default;













import pageHead from '../../components/page-head.vue'
export default {
	data() {
		return {
			title:'news',
			newsList: []
		}
	},
	onLoad() {
		this.news();
	},
	components: {
		pageHead
	},
	methods: {
		news() {
			uni.request({
				url: 'http://dianli.hbnrtech.com/powerArea/actionManage/nuusAction/nuusAll.html?channelId=2',
				data: '',
				success: (data) => {
					if (data.data.code == 0) {
						data.data.data.push({
							title: ''
						})
						this.newsList = data.data.data;
					}
				},
				fail: (data, code) => {
					console.log('fail' + JSON.stringify(data));
				}
			})
		},
		goDetail: function (e) {
			console.log(1)
			uni.navigateTo({
				url: "details.vue"
			})
		},

	}
}



